/* eslint-disable class-methods-use-this */
import AppModel from '../models/AppModel';
import AppView from '../views/AppView';
import CreateElements from './CreateElements';

export default class App {
  constructor() {
    this.requestInfo = {
      // API_KEY: 'AIzaSyBieBEtePDgoxNM7RGrswogEG3z__QgcDo&type',
      API_KEY: 'AIzaSyBbWnY9aBf7_U99aeuln0_u6DjYqE3Z0BI&type',
      name: '',
      nextPageToken: '',
    };
    this.itemsCounter = 0;
    this.i = 0;
    this.clientX = null;
    this.locked = false;
    this.windowWidth = 0;
    this.ini = 0;
    this.fin = 0;
    this.itemsOnPage = 0;
  }

  start() {
    const root = CreateElements.createRoot();
    const input = CreateElements.createInput();
    const slider = CreateElements.createSlider();
    const controlButtons = CreateElements.createControlButtons();
    this.addEventListeners(input, slider, controlButtons);
    root.appendChild(input);
    root.appendChild(slider);
    root.appendChild(controlButtons);
    document.body.appendChild(CreateElements.createLogo('Youtube-Client'));
    this.size();
    this.model = new AppModel(this.requestInfo);
    this.view = new AppView();
    this.slider = slider;
  }

  async renderItems() {
    document.querySelectorAll('.logo-span').forEach(e => e.classList.add('logo-span-animate'));
    const data = await this.model.getInfo();
    const { itemsInfo } = data;
    this.requestInfo.nextPageToken = data.nextPageToken;
    this.itemsCounter += itemsInfo.length;
    this.view.render(itemsInfo);
    this.slider.style.zIndex = 1;
    this.showButtons();
    if (this.itemsCounter === 0) {
      document.body.removeChild(document.querySelector('.logo'));
      document.body.appendChild(CreateElements.createLogo('Nothing'));
    }
  }

  getIndexToNextRequest() {
    return !!(this.i !== 0 && this.i + 3 >= (this.itemsCounter / this.itemsOnPage));
  }

  reRender() {
    document.body.removeChild(document.querySelector('.logo'));
    while (this.slider.firstChild) {
      this.slider.removeChild(this.slider.firstChild);
    }
    this.itemsCounter = 0;
    this.requestInfo.nextPageToken = '';
    this.i = 0;
    document.body.appendChild(CreateElements.createLogo('Youtube-Client'));
    this.renderItems();
    this.setButtonsValue();
    this.slider.style.setProperty('--i', 0);
    document.querySelector('.current').classList.add('hidden');
  }

  size() {
    this.windowWidth = window.innerWidth;
    this.setItemsOnpage();
    this.setCorrectPage();
    this.leftItem = ((this.i + 1) * this.itemsOnPage) - this.itemsOnPage;
  }

  setItemsOnpage() {
    if (this.windowWidth > 1500) {
      this.itemsOnPage = 4;
      this.transition = 'transform .5s ease-in-out';
    }
    if (this.windowWidth < 1500 && this.windowWidth > 1200) {
      this.itemsOnPage = 3;
      this.transition = 'transform .4s ease-in-out';
    }
    if (this.windowWidth < 1200 && this.windowWidth > 700) {
      this.itemsOnPage = 2;
      this.transition = 'transform .4s ease-in-out';
    }
    if (this.windowWidth < 700 && this.windowWidth > 350) {
      this.itemsOnPage = 1;
      this.transition = 'transform .3s ease-in-out';
    }
  }

  setCorrectPage() {
    if (this.leftItem) {
      requestAnimationFrame(() => this.slider.style.setProperty('--i', Math.floor(this.leftItem / this.itemsOnPage)));
      this.i = Math.floor(this.leftItem / this.itemsOnPage);
      this.setButtonsValue();
    }
  }

  unify(e) {
    return e.changedTouches ? e.changedTouches[0] : e;
  }

  lock(e) {
    this.slider.style.transition = 'none';
    this.slider.style.cursor = 'grabbing';
    this.clientX = this.unify(e).clientX;
    this.locked = true;
  }

  move(e) {
    if (this.locked) {
      if (this.getIndexToNextRequest()) this.renderItems();
      const shiftX = this.unify(e).clientX - this.clientX;
      const s = Math.sign(shiftX);
      let f = +(s * shiftX / this.windowWidth).toFixed(2);
      this.ini = this.i - s * f;
      if ((this.i > 0 || s < 0)
      && (this.i + 1 < (this.itemsCounter / this.itemsOnPage) || s > 0) && f > 0.2) {
        this.i -= s;
        f = 1 - f;
      }
      this.fin = this.i;
      this.ani(this.ini + (this.fin - this.ini));
      this.clientX = null;
      this.locked = false;
      this.slider.style.cursor = 'grab';
    }
  }

  ani(value) {
    this.slider.style.transition = this.transition;
    requestAnimationFrame(() => {
      this.setButtonsValue();
      this.slider.style.setProperty('--i', value);
    });
    this.leftItem = ((this.i + 1) * this.itemsOnPage) - this.itemsOnPage;
  }

  drag(e) {
    e.preventDefault();
    if (this.locked) {
      const shiftX = ((this.unify(e).clientX - this.clientX) / this.windowWidth).toFixed(2);
      requestAnimationFrame(() => {
        this.slider.style.setProperty('--i', this.i - shiftX);
      });
    }
  }

  ButtonsHandler(e) {
    e.preventDefault();
    e.stopPropagation();
    if (e.target.classList.contains('current')) return;
    if (this.getIndexToNextRequest()) this.renderItems();
    this.i = +e.target.value;
    this.ani(this.i);
  }

  showTooltip(e) {
    if (e.target.classList.contains('current')) return;
    if (document.querySelector('.next').firstChild.innerHTML === '') this.setButtonsValue();
    e.target.firstChild.classList.remove('hidden');
  }

  hideTooltip(e) {
    if (e.target.classList.contains('current')) return;
    e.target.firstChild.classList.add('hidden');
  }

  setButtonsValue() {
    const beforePrev = document.querySelector('.bfPrev');
    const prev = document.querySelector('.prev');
    const current = document.querySelector('.current');
    const next = document.querySelector('.next');
    if (this.i >= 1) prev.classList.remove('hidden');
    else prev.classList.add('hidden');
    if (this.i >= 2) beforePrev.classList.remove('hidden');
    else beforePrev.classList.add('hidden');
    if (this.i + 1 >= (this.itemsCounter / this.itemsOnPage)) next.classList.add('hidden');
    if (this.i + 1 === this.itemsCounter && this.itemsOnPage === 1) next.classList.add('hidden');
    if (this.i + 1 < (this.itemsCounter / this.itemsOnPage)) next.classList.remove('hidden');
    current.value = this.i;
    current.firstChild.innerHTML = this.i + 1;
    current.innerHTML = this.i + 1;
    next.value = this.i + 1;
    next.firstChild.innerHTML = this.i + 2;
    prev.value = this.i - 1;
    prev.firstChild.innerHTML = this.i;
    beforePrev.value = this.i - 2;
    beforePrev.firstChild.innerHTML = this.i - 1;
  }

  showButtons() {
    if (this.itemsCounter > 0) document.querySelector('.current').classList.remove('hidden');
    if (this.itemsCounter > this.itemsOnPage && this.i + 1 < (this.itemsCounter / this.itemsOnPage)) document.querySelector('.next').classList.remove('hidden');
  }

  addEventListeners(input, slider, controlButtons) {
    input.addEventListener('change', (e) => {
      this.requestInfo.name = e.target.value;
      if (this.itemsCounter) this.reRender();
      else this.renderItems();
      document.querySelector('.search-btn').blur();
    });
    input.addEventListener('submit', (e) => {
      this.requestInfo.name = e.target.value;
      if (this.itemsCounter) this.reRender();
      else this.renderItems();
    });
    input.addEventListener('focusin', (e) => {
      e.target.value = '';
    });
    window.addEventListener('resize', e => this.size(e), false);
    slider.addEventListener('mousedown', e => this.lock(e), false);
    slider.addEventListener('mouseleave', (e) => {
      this.requestInfo.lock = false;
      this.move(e);
    }, false);
    slider.addEventListener('touchstart', e => this.lock(e), false);
    slider.addEventListener('mousemove', e => this.drag(e), false);
    slider.addEventListener('touchmove', e => this.drag(e), false);
    slider.addEventListener('mouseup', e => this.move(e), false);
    slider.addEventListener('touchend', e => this.move(e), false);
    controlButtons.childNodes.forEach((e) => {
      e.addEventListener('click', evt => this.ButtonsHandler(evt), false);
      e.addEventListener('mousedown', evt => this.showTooltip(evt), false);
      e.addEventListener('mouseup', evt => this.hideTooltip(evt), false);
      e.addEventListener('touchstart', evt => this.ButtonsHandler(evt), false);
      e.addEventListener('mouseleave', evt => this.hideTooltip(evt), false);
    });
  }
}
