/* eslint-disable class-methods-use-this */
import './app-view.css';

export default class AppView {
  createElements(elem) {
    const wrapper = document.createElement('li');
    wrapper.classList.add('item-wrapper');
    const item = document.createElement('div');
    item.classList.add('item');
    const title = document.createElement('div');
    title.style.background = `url(${elem.img}) top no-repeat`;
    title.style.backgroundSize = 'cover';
    title.classList.add('title');
    const name = document.createElement('a');
    name.innerHTML = elem.title;
    name.href = `https://www.youtube.com/watch?v=${elem.id}`;
    name.setAttribute('target', '_blank');
    name.classList.add('title-name');
    title.appendChild(name);
    const author = document.createElement('div');
    author.classList.add('author');
    const authorName = document.createElement('p');
    authorName.innerHTML = elem.author;
    const authorFA = document.createElement('span');
    authorFA.classList.add('fas', 'fa-male');
    author.appendChild(authorFA);
    author.appendChild(authorName);
    const date = document.createElement('div');
    date.classList.add('date');
    const dt = document.createElement('p');
    dt.innerHTML = elem.date;
    const dateFA = document.createElement('span');
    dateFA.classList.add('fas', 'fa-calendar-alt');
    date.appendChild(dateFA);
    date.appendChild(dt);
    const vievCount = document.createElement('div');
    vievCount.classList.add('viewCount');
    const count = document.createElement('p');
    count.innerHTML = elem.viewCount;
    const viewFA = document.createElement('span');
    viewFA.classList.add('fas', 'fa-eye');
    vievCount.appendChild(viewFA);
    vievCount.appendChild(count);
    const description = document.createElement('div');
    description.classList.add('description');
    const descriptionTxt = document.createElement('p');
    descriptionTxt.innerHTML = elem.description;
    description.appendChild(descriptionTxt);
    item.appendChild(title);
    item.appendChild(author);
    item.appendChild(date);
    item.appendChild(vievCount);
    item.appendChild(description);
    wrapper.appendChild(item);
    return wrapper;
  }

  render(itemsInfo) {
    const slider = document.querySelector('.slider');
    const fragment = document.createDocumentFragment();
    itemsInfo.forEach(e => fragment.appendChild(this.createElements(e)));
    requestAnimationFrame(() => {
      slider.appendChild(fragment);
    });
  }
}
