export default class AppModel {
  constructor(state) {
    this.state = state;
  }

  static createItems(itemsInfo, viewCounts) {
    const items = [];
    itemsInfo.forEach((e, i) => {
      const item = {};
      item.title = e.snippet.title;
      item.link = `https://www.youtube.com/watch?v=${e.id.videoId}`;
      item.id = e.id.videoId;
      item.img = e.snippet.thumbnails.medium.url;
      item.date = new Date(e.snippet.publishedAt).toLocaleDateString().replace(/[.]/g, '-');
      let { description } = e.snippet;
      if (description.length < 4) description = 'No description :(';
      if (description.length > 65) description = `${description.substr(0, 65)}...`;
      item.description = description;
      item.author = e.snippet.channelTitle;
      item.viewCount = viewCounts[i].statistics.viewCount;
      items.push(item);
    });
    return items;
  }

  async getInfo() {
    const info = {};
    await fetch(`https://www.googleapis.com/youtube/v3/search?key=${this.state.API_KEY}=video&part=snippet&maxResults=15&q=${this.state.name}${this.state.nextPageToken ? `&pageToken=${this.state.nextPageToken}` : ''}`)
      .then(response => response.json())
      .then((data) => {
        info.nextPageToken = data.nextPageToken;
        info.id = data.items.map(e => e.id.videoId);
        info.data = data.items;
        return fetch(`https://www.googleapis.com/youtube/v3/videos?key=${this.state.API_KEY}&id=${info.id.toString()}&part=snippet,statistics`);
      })
      .then(response => response.json())
      .then((viewCounts) => {
        info.viewCounts = viewCounts.items;
        info.itemsInfo = AppModel.createItems(info.data, info.viewCounts);
      })
      .catch(e => window.console.log(e));
    return info;
  }
}
