import './style.css';

export default class CreateElements {
  static createRoot() {
    const root = document.createElement('div');
    root.classList.add('root');
    document.body.appendChild(root);
    return root;
  }

  static createSlider() {
    const slider = document.createElement('ul');
    slider.classList.add('slider');
    return slider;
  }

  static createInput() {
    const wrapper = document.createElement('div');
    wrapper.classList.add('search-btn-wrapper');
    const input = document.createElement('input');
    input.classList.add('search-btn');
    input.autofocus = true;
    input.placeholder = 'What kind of video do you want to find?';
    wrapper.appendChild(input);
    return wrapper;
  }

  static createLogo(txt) {
    const logo = document.createElement('div');
    logo.classList.add('logo');
    txt.split('').forEach((e) => {
      const span = document.createElement('span');
      span.classList.add('logo-span');
      span.innerHTML = e;
      logo.appendChild(span);
    });
    return logo;
  }

  static createControlButtons() {
    const controlButtons = document.createElement('div');
    controlButtons.classList.add('controlPanel');
    const bfPrev = document.createElement('button');
    bfPrev.classList.add('bfPrev', 'hidden');
    bfPrev.value = 0;
    const prev = document.createElement('button');
    prev.classList.add('prev', 'hidden');
    prev.value = 0;
    const current = document.createElement('button');
    current.classList.add('current', 'hidden');
    current.value = 0;
    current.innerHTML = 1;
    const next = document.createElement('button');
    next.value = 1;
    next.classList.add('next', 'hidden');
    controlButtons.appendChild(bfPrev);
    controlButtons.appendChild(prev);
    controlButtons.appendChild(current);
    controlButtons.appendChild(next);
    controlButtons.childNodes.forEach((e) => {
      const tooltip = document.createElement('span');
      tooltip.classList.add('hidden', 'tooltip');
      e.appendChild(tooltip);
    });
    return controlButtons;
  }
}
